@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,300i,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:300,300i,400,500,700);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,300i,400i,500,600,700,700i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.introduction {
  padding: 50px;
  display: flex;
  width: 70%;
  margin: 0 auto;
}

.pic-wrapper {
  padding: 50px 0px 50px 0px;
}

.profileimg {
  height: 35vh;
  border-radius: 10px;
}

.shortdesc {
  padding: 70px;
}

.section-header {
  width: 100%;
  justify-content: center;
  display: flex;
}

h2.section-header-text {
  font-family: "Josefin Sans";
  width: 80%;
  text-align: center;
  border: 0px solid  rgb(246, 99, 73);
  background-color:  rgb(246, 99, 73);
  line-height: 0.1em;
  margin: 10px 0 0px 0px;
  font-size: 30px;
  font-style: italic;
  font-weight: 700;
  color:  rgb(246, 99, 73);
}

h2 span {
  background: #fff;
  padding: 0 10px;
}

.section-body {
  padding: 2vh 0 1vh 0;
  font-family: "Raleway";
  font-weight: 400;
  line-height: 1.5em;
  text-align: justify;
  color: rgb(44, 44, 44);
}

.bio-wrapper {
  font-family: "Raleway";
  font-size: 15px;
  font-weight: 400;
}

.bio-info {
  padding: 3px 0px 3px 0px;
  border-bottom: 0.5px solid rgb(233, 232, 232);
  color: rgb(47, 47, 47);
}

a {
  color: black;
  text-decoration: none;
}

a :hover {
  /* color: crimson; */
}

.cv-wrapper {
  display: flex;
  justify-content: flex-end;
  padding-top: 20px;
  padding-right: 20px;
}

.cv {
  padding: 15px 50px 15px 50px;
  border-radius: 5px;
  border: none;
  background-color: rgb(246, 99, 73);
  color: white;
  font-family: "Raleway";
  font-weight: 700;
  font-size: 15px;
  box-shadow: 2px 5px #fbab86;
}

.cv-button-wrapper {
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.cv-button-wrapper:hover {
  -webkit-transform: translate(0px, -5px);
          transform: translate(0px, -5px);
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
}

.cv {
  /* font-size: 200px; */
}

.bolded-words {
  font-weight: 700;
  color: rgb(47, 47, 47);
}

.education {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(253, 249, 247);
  padding: 50px 0px 50px 0px;
  font-family: "Ubuntu";
}
.workexperience {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0px 50px 0px;
  font-family: "Ubuntu";
}

.logo {
  display: flex;
