/* Header */
.header {
  height: 100vh;
  width: 100%;

  background-image: url(/Darryl-Leong/static/media/background.d94595fa.JPG);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.header-wrapper {
  height: 100%;
  width: 100%;

  background-color: rgba(0, 0, 0, 0.3);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header-text-first {
  text-align: left;
  color: white;
  font-size: 50px;
  font-style: italic;
  font-weight: 300;
  font-family: "Josefin Sans";
  padding: 10px 0px 10px 0px;
}

.header-text-second {
  margin: 0;
  padding: 0;

  color: rgb(255, 255, 255);
  font-size: 60px;
  font-weight: 700;
  /* font-style: italic; */

  padding: 30px 30px 20px 30px;
  border: none;
  background-color: rgb(106, 181, 251);
  /* line-height: 1.1em; */
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

/* .header-text-second:hover {
  background-color: rgb(63, 150, 200);
  transition: 0.5s;
} */

.sm-wrapper {
  position: absolute;
  bottom: 50px;
}

.sm-icon {
  margin: 5px;
  padding: 5px;
  color: rgb(63, 150, 200);
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background-color: white;
  border-radius: 50%;
}

.sm-icon:hover {
  cursor: pointer;
  color: white;
  background-color: rgb(63, 150, 200);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
